<template><section><html><head></head><body><h1>PointCollection</h1>
<p><code v-pre="">vc-collection-primitive-point</code> 组件用于加载点图元集合。</p>
<h2>示例</h2>
<h3>添加海量点到场景</h3>
<h4>预览</h4>
<doc-preview>
  <template>
    <div class="viewer">
      <vc-viewer @ready="ready">
        <vc-collection-primitive-point :points="points" @click="clicked"></vc-collection-primitive-point>
        <vc-collection-primitive-point @click="clicked">
          <template v-for="(polyline, index) of polylines">
            <template v-for="(position, subIndex) of polyline.positions">
              <vc-primitive-point :position="position" :key="'point' + index + 'position' + subIndex" :color="colorPoint" :pixel-size="32"></vc-primitive-point>
            </template>
          </template>
        </vc-collection-primitive-point>
      </vc-viewer>
    </div>
  </template>
  
</doc-preview>
